#booking h3 {
	font-size: 1.5em;
	margin: 0;
}
.bookingContainer {
	background-color: rgba(0, 100, 50, 0.2);
}

.bookingForm {
	box-sizing: border-box;
	margin: 0 auto;
	padding: 2em;
	max-width: 533px;
	display: grid;
	grid-template-columns: 40% 60%;
	align-items: center;
}

#booking .bookingInputs {
	padding: 1em;
	margin: 1em;
}
#booking .bookingLabels {
	margin: 0 1em;
}
#booking button {
	grid-column: 2/3;
	font-size: 1em;
	padding: 1em;
	margin: 1em;
	background-color: rgb(0, 100, 50);
	color: rgb(255, 255, 255);
	border: none;
}

#nameDropDown {
	margin: 1em;
	display: flex;
	position: relative;
}
#nameDropDown #query {
	margin: 0;
	width: 70%;
}

#nameDropDown button {
	width: 20%;
	margin: 0;
	padding: 0;
}
.dropDown {
	position: absolute;
	display: none;
	top: 3em;
	right: 0;
	background-color: rgb(255, 255, 255);
	border: 1px solid black;
	border-bottom: none;
}
.dropDown li {
	padding: 0.5em;
	border-bottom: 1px solid black;
}
